export class AssetsComponent implements OnInit, OnDestroy {
  products = [];
  allProducts = [];
  products_id = [];
  dateGraph:string[] = [];
  dataGraph = [];
  volumeGraph = [];
  data: BloxResponse;
  showgraph: boolean = false;
  disableButton: boolean = false;
  public subscription: Subscription = new Subscription();
  flagAction: boolean = false;
  item: any;
  timeout: any;
  transactions: any;
  datainicial: string = "1h";
  client_config:any;
  activebtn: boolean= true;
  formatdate:boolean = false;

  @ViewChild("main") myChart: echarts.EChartsType = null;

  constructor(private spinner: NgxSpinnerService, private dialog: MatDialog, private toastr: ToastrService,
    public datepipe: DatePipe, private graphservice: GraphAssetsService, private market: MarketService, 
    private router: Router, private manage_data: ManageDataService<any>, private assetsService: AssetsService, 
    private portafolioService: PortafolioService, private socket: SocketioService, private translate:TranslateService,
    private client:ClientService) { }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (this.myChart != undefined) {
      this.myChart.resize()
    }
  }

  async changeTime(date: string) {
    let timeZone;
    if (typeof Intl === 'object' && typeof Intl.DateTimeFormat === 'function') {
      timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
    }

    let stop = new Date(Date.now());
    let start = new Date(Date.now());
    this.datainicial = date;
    var granular = "";
    if (date == "10m") {
      start.setMinutes(start.getMinutes() - 10);
      granular = "MINUTE";
      this.formatdate = false;
    }
    if (date == "1h") {
      start.setHours(start.getHours() - 1);
      granular = "MINUTE";
      this.formatdate = false;

    }
    if (date == "1d") {
      start.setDate(start.getDate() - 1);
      granular = "FIFTEEN_MIN";
      this.formatdate = false;
    }
    if (date == "1w") {
      start.setDate(start.getDate() - 7);
      granular = "DAY";
      this.formatdate = true;

    }
    if (date == "1M") {
      start.setMonth(start.getMonth() - 1);
      granular = "DAY";
      this.formatdate = true;

    }
    if (date == "3M") {
      start.setMonth(start.getMonth() - 3);
      granular = "DAY";
      this.formatdate = true;

    }
    if (date == "6M") {
      start.setMonth(start.getMonth() - 6);
      granular = "DAY";
      this.formatdate = true;

    }
    if (date == "ALL") {
      start.setMonth(start.getMonth() - 8);
      granular = "DAY";
      this.formatdate = true;

    }


    if (this.item != null) {
      if (granular != "") {
        var data: HistoricalCandles = {
          ticker: this.item.id,
          granularity: granular,
          start: start.getTime(),
          end: stop.getTime(),
        }
        this.dateGraph = []
        this.dataGraph = []
        this.volumeGraph = []
        var datareturn = await this.market.GetHistoricalCandles(data).toPromise();
        var newdata = JSON.parse(datareturn.payload)
        newdata.forEach(element => {
          let date = new Date(element[0] * 1000);
          let tzDate = new Date(date.toLocaleString('en-US', { timeZone: timeZone }));
          let latest_date = this.datepipe.transform(new Date(tzDate.getTime()), 'yyyy-MM-dd hh:mm');

          var datos = [element[3] * 0.9899, element[4] * 0.9899, element[1] * 0.9899, element[2] * 0.9899]
          this.dateGraph.unshift(latest_date)
          this.dataGraph.unshift(datos)
          this.volumeGraph.unshift(element[5])

        });

        this.InitChart();
      }
    }
  }



  async InitChart() {

    this.myChart = echarts.init((document.getElementById('main')) as any, 'dark');
    var option = this.graphservice.makeOption();

    if(this.formatdate){
      option.xAxis[0].axisLabel.formatter = function (value) {
        return echarts.format.formatTime('MM-dd', value);
      };
    }else{
      option.xAxis[0].axisLabel.formatter = function (value) {
        return echarts.format.formatTime('hh:mm', value);
      };
    } 

    if (localStorage.getItem("theme") == "dark") {
      option.backgroundColor = 'rgb(255, 255, 255)'
      option.xAxis[0].axisLabel.textStyle.color = 'rgb(0, 0, 0)'
      option.yAxis[0].axisLabel.textStyle.color = 'rgb(0, 0, 0)'
    } else {
      option.backgroundColor = 'rgb(0, 0, 0)'
      option.xAxis[0].axisLabel.textStyle.color = 'rgb(255, 255, 255)'
      option.yAxis[0].axisLabel.textStyle.color = 'rgb(255, 255, 255)'
    }

    option.tooltip.formatter = function (param) {
      var languageCountryCode = "en-US";
      let categoryNum = param[0].data[0];
      let xValue = option.xAxis[0].axisLabel.formatter(option.xAxis[0].data[categoryNum]);
      let res = xValue;
      const options = { style: 'currency', currency: 'USD' };
      const numberFormat = new Intl.NumberFormat(languageCountryCode, options);
      const numberFormatVolume = new Intl.NumberFormat(languageCountryCode, {});
      for (let i = param.length - 1; i >= 0; i--) {
        let seriesName = param[i].seriesName;
        let value = param[i].data;
        if (value != "-") {
          var data = param[i].data;
          if (seriesName != "Volume") {
            const openPrice = data[1];
            const closePrice = data[2];
            const lowestPrice = data[3];
            const highestPrice = data[4];
            var imageName;
            if (openPrice > closePrice) {
              imageName = '../assets/Graph/sellTradePrice.png';
            } else {
              imageName = '../assets/Graph/buyTradePrice.png';
            }
            const openPriceStr = numberFormat.format(openPrice);
            const closePriceStr = numberFormat.format(closePrice);
            const lowestPriceStr = numberFormat.format(lowestPrice);
            const highestPriceStr = numberFormat.format(highestPrice);

            let image = `<p><div style="height: 6px;width: 180px">
                    <img src="${imageName}" style="vertical-align: middle;" width="20px"/>
                    <span style="vertical-align: middle;color:white;">${"open"}:</span>
                    <span style="vertical-align: middle;color:white;float: right">${openPriceStr}</span>
                    </div>`;
            res += (image + "</p><div style=\"line-height:0;\"><br></div>");
            let image1 = `<p><div style="height: 10px;width: 180px">
                    <img src="${imageName}" style="vertical-align: middle;" width="20px"/>
                    <span style="vertical-align: middle;color:white;">${"close"}:</span>
                    <span style="vertical-align: middle;color:white;float: right">${closePriceStr}</span>
                    </div>`;
            res += (image1 + "</p><div style=\"line-height: 0;\"><br></div>");
            let image2 = `<p><div style="height:10px;width: 180px">
                    <img src="${imageName}" style="vertical-align: middle;" width="20px"/>
                    <span style="vertical-align: middle;color:white;">${"Low"}:</span>
                    <span style="vertical-align: middle;color:white;float: right">${lowestPriceStr}</span>
                    </div>`;
            res += image2 + "</p><div style=\"line-height:0;\"><br></div>";

            let image3 = `<p><div style="height: 10px;width: 180px">
                    <img src="${imageName}" style="vertical-align: middle;" width="20px"/>
                    <span style="vertical-align: middle;color:white;">${"High"}:</span>
                    <span style="vertical-align: middle;color:white;float: right">${highestPriceStr}</span>
                    </div>`;
            res += image3 + "</p><div style=\"line-height:0;\"><br></div>";
          } else {
            let volume = data;
            let volumeStr = numberFormatVolume.format(volume);
            let imageName = '../assets/Graph/volume.png';
            let image = `<p><div style="height: 20px;width: 180px">
                    <img src="${imageName}" style="vertical-align: middle;" width="20px"/>
                    <span style="vertical-align: middle;color:white;">${"volume"}:</span>
                    <span style="vertical-align: middle;color:white;float: right">${volumeStr}</span>
                    </div>`;
            res += image + "<div style=\"line-height:0;\"><br></div>";
          }
        }
      }
      return res;
    },
    console.log("fechas "+typeof(this.dateGraph))
      option.xAxis[0].data = this.dateGraph;
    option.xAxis[1].data = this.dateGraph;
    option.series[0].data = this.dataGraph;
    option.series[1].data = this.volumeGraph;
    option.animation = true;
    this.showgraph = true;
    this.myChart.setOption(option, true);


  }

  onButtonGroupClick($event) {
    let clickedElement = $event.target || $event.srcElement;
    if (clickedElement.nodeName === "BUTTON") {
      let isCertainButtonAlreadyActive = clickedElement.parentElement.querySelector(".active");
      if (isCertainButtonAlreadyActive) {
        isCertainButtonAlreadyActive.classList.remove("active");
      }
      clickedElement.className += " active";
    }
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }


  async ngOnInit(){
    this.spinner.show();
    this.client_config = await this.GetClientConfig();
    this.SetPortfolioData();
    this.GetEvent();
  }

  GetEvent() {
    this.subscription.add(this.manage_data.getSubject(EnumEventsPopUp.ChangeColor.toString()).subscribe(data => {
      if (data != null) {
        this.changeTime(this.datainicial);

      }
    }))
  }

  async datosSocket(busq: string) {
    await this.socket.InitBlox(this.products_id);
    this.subscription.add(this.socket.GetDataBlox().subscribe(data => {
      try {
        if (typeof data === 'string') {
          this.data = JSON.parse(data);
          this.SetValues(this.data);
          
        }
        else {
          // console.warn("wrong data", data);
        }

      } catch (error) {
        console.error(error);
      }
    }));
  }

  async GetDataBloxOrder() {
    this.subscription.add(this.socket.GetDataBloxOrder().subscribe((data: any) => {
      if (typeof data != 'object' && data != "Hey Dude!") {
        if (JSON.parse(data).success == null || JSON.parse(data).success == undefined) {
          this.ResponseEvent(JSON.parse(data))
        }
      }
    }))
  }
  SetValues(data: any) {

    let aux = this.products.find(x => x.id === data.product_id);
    if (aux) {
      if (aux.price != data.mid_price) {
        aux.price = data.mid_price;
        var residuo = aux.price - aux.open;
        aux.percentage = (residuo * 100) / aux.price;
      }
      if (aux.bid != data.best_bid) {
        aux.bid = data.best_bid;
      }
      if (aux.ask != data.best_ask) {
        aux.ask = data.best_ask;
      }
    }
    aux = this.allProducts.find(x => x.id === data.product_id);
    if (aux) {
      if (aux.price != data.mid_price) {
        aux.price = data.mid_price;
        var residuo = aux.price - aux.open;
        aux.percentage = (residuo * 100) / aux.price;
      }
    }
  }

  async SetPortfolioData() {
    await this.SetList();
    await this.datosSocket('')
    await this.GetDataBloxOrder();
    this.spinner.hide();

  }


  ResponseEvent(msg: any) {
    switch (msg.header.msg_type) {
      case 'EXECUTION':
      case 'TRACKING_EXECUTION':
      case 'DEPOSIT':
      case 'WITHDRAW':
      case 'ORDER_ERROR':
      case 'ORDER_CANCEL':
      case 'ORDER_REJECTED':
      case 'TRACKING_EXECUTION':

        break;
      default:
        break;
    }
  }

  async SetList() {
    this.products_id = [];
    let logosResponse = await this.GetAllLogos();
    let productResponse = await this.GetAllProducts();

    if (productResponse) {
      productResponse = productResponse.filter((x) => x.quote_currency === "USD" && x.trading_disabled === false);
      productResponse = productResponse.map((x) => ({
        ...x,
        logo: logosResponse.filter((l) => l.symbol === x.base_currency).map(x => x.logo),
        price: 0,
        ask: 0,
        bid: 0,
        percentage: 0
      }));

      productResponse = productResponse.sort((a, b) => {
        if (a.base_currency > b.base_currency) return 1;
        else return -1;
      });
      await this.GetOpenData(productResponse);
      this.products = productResponse;
      this.allProducts = JSON.parse(JSON.stringify(productResponse));
      this.products_id = productResponse.map(x => x.id);

    }

  }

  async GetOpenData(productResponse: any) {

    await Promise.all(productResponse.map(async (item) => {
      let response = await this.GetMarketProducts(item.id).then();
      if (response.success) {
        let data:Product24hrPayloadModel = JSON.parse(response.payload);
        item.open = data.open;
      }
    }));
  }

  async GetMarketProducts(asset: any) {
    const data: any = {
      ticker: asset
    }
    return await this.market.GetProduct24hrStats(data).toPromise();
  }

  async GetAllProducts() {
    const data = await this.portafolioService.GetAllProducts().toPromise();
    return data;
  }


  async GetAllLogos() {
    const data = await this.assetsService.GetAllLogosURL().toPromise();
    return data;
  }

  async GetClientConfig(): Promise<ClientConfigResponse>{
    return new Promise((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe((data:ClientConfigResponse)=>{
        resolve(data);
      },err=>{
        console.log("Error GetClientConfig:", err);
        resolve(null);
      }));   
    });
  }

  search(valueb: string) {

    let lista = this.allProducts.filter((x) => x.base_currency.toLowerCase().startsWith(valueb.toLowerCase()));
    this.products = lista;

  }
  stablecoin(stable: boolean) {
    this.activebtn=false;
    let lista = this.allProducts.filter((x) => x.fx_stablecoin === stable);
    this.products = lista;

  }

 
  Buy(product: any) {
    this.disableButton = true;
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.maxWidth= '100vw';
    dialogConfig.width = "auto";

    const data: any = {
      logo: product.logo[0],
      symbol: product.base_currency,
      price: product.price,
      ask: product.ask,
      bid: product.bid,
      action: "buy"
    }
    if(this.client_config.id_verified){
      this.manage_data.emitEvent(EnumEventsPopUp.buy_sell.toString(), data);
      let dialog = this.dialog.open(BuySellComponent,dialogConfig);
      this.subscription.add(dialog.afterClosed().subscribe(()=>{
        this.disableButton = false;
      }))
    }else{
      this.ConfirmValidateID();
    }
  }

  Sell(product: any) {
    this.disableButton = true;
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.maxWidth= '100vw';
    dialogConfig.width = "auto";

    const data: any = {
      logo: product.logo[0],
      symbol: product.base_currency,
      price: product.price,
      ask: product.ask,
      bid: product.bid,
      action: "sell"
    }
    if(this.client_config.id_verified){
      this.manage_data.emitEvent(EnumEventsPopUp.buy_sell.toString(), data);
      let dialog = this.dialog.open(BuySellComponent,dialogConfig);
      
      this.subscription.add(dialog.afterClosed().subscribe(()=>{
        this.disableButton = false;
      }))
    }else{
      this.ConfirmValidateID();
    }
  }

  actionassest(product: any) {
    this.flagAction = !this.flagAction;
    this.item = product;
    if (this.myChart === undefined) {
      this.changeTime(this.datainicial);
    } else {
      this.myChart.clear();
    }

    
    if(this.flagAction){
      // Hack: Scrolls to top of Page after page view initialized
      let top = document.getElementById('top');
      if (top !== null) {
        console.log("show coin...");
        top.scrollIntoView();
        top = null;
      }
  }
  }
  //************************************************************************************
  //Confirm Validate ID
  //************************************************************************************


  async ConfirmValidateID(): Promise<void> {

    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = false;
    dialogConfig.disableClose = true;
    // dialogConfig.height = "20rem";
    dialogConfig.width = "40rem";

    var data = {
      title: await this.translate.get("Errores.mustValidateID").toPromise(),
      icon: 'quest',
      conten: await this.translate.get("Errores.wantValidateID").toPromise()
    }
    this.manage_data.emitEvent<confirm>(EnumEventsPopUp.ConfirmAction.toString(), data);
    let dialogRef = this.dialog.open(ConfirmationComponent, dialogConfig);

    this.subscription.add(dialogRef.afterClosed().subscribe(result => {
      if (result == EnumConfirmation.si.toString()) {
        this.router.navigate(['/vouched']);
      }

    }));
  }

  //===========================================================================
  // Show Error
  //===========================================================================

  ShowError(msg: string, title: string = "", disableTimeOut: boolean = true) {
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,
      positionClass: 'toast-center-center',
    });
  }


}

