import { Injectable } from '@angular/core';
import * as echarts from 'echarts';

@Injectable({
    providedIn: 'root'
})
export class GraphAssetsService {

    constructor() { }


    public makeOption() {
        var colorgraph = ""
        var coloraxisx = ""

        return {
            animation: true,
            grid: [
                {
                    left: '3%',
                    right: '0',
                    height: '70%',
                    width:'90%',
                    top: 5,
                },
                {
                    left: '3%',
                    right: '0',
                    top: '80%',
                    bottom: '2%',
                    width:'90%',

                }
            ],
            tooltip: {
                extraCssText: 'width: 23vh',
                trigger: 'axis',
                axisPointer: {
                    type: 'line'
                },
                borderColor: 'rgba(0, 0, 0, 1.0)',
                backgroundColor: 'rgba(32, 32, 32, 0.80)',
                textStyle: {
                    color: 'rgba(255, 255, 255, 1.0)',
                },
                formatter: function (param) {
                    return echarts.format.formatTime('MM-dd hh:mm', param);
                },
                position: function (pos, params, el, elRect, size) {
                    var obj = { top: 0 };
                    obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 0;
                    return obj;
                },
            },
            axisPointer: {
                link: { xAxisIndex: 'all' },
                label: {
                    backgroundColor: '#777'
                },
                hideDelay: 0,
            },
            xAxis: [
                {
                    data: [],
                    axisLine: {
                        show: false,                        
                    },
                    splitLine: {
                        show: false,
                    },
                    axisLabel: {
                        formatter: function (value) {
                            return echarts.format.formatTime('MM-dd hh:mm', value);
                        },
                        textStyle: {                            
                            fontSize: 12,
                            color: coloraxisx,
                        },
                        margin: 15,
                    },
                    axisTick: {
                        show: false,
                        length: 12,
                        alighWithLabel: true,
                    },
                    min: 'dataMin',
                    max: 'dataMax',
                },
                {
                    gridIndex: 1,
                    data: [],
                    axisLine: {
                        onZero: false

                    },
                    axisTick: { show: false },
                    splitLine: { show: false },
                    axisLabel: { show: false },
                    min: 'dataMin',
                    max: 'dataMax',
                }],
            yAxis:
                [
                    {
                        axisLabel: {
                            textStyle: {
                                fontSize: 12,
                                color: coloraxisx,
                            },
                            inside: true,
                            verticalAlign: 'top',
                            margin: 0,
                            showMinLabel: false,
                            showMaxLabel: false,
                        },
                        zlevel: 1000,
                        min: 'dataMin',
                        max: 'dataMax',
                        position: 'right',
                        axisTick: {
                            show: false
                        },
                        splitLine: { show: false }
                    },
                    {
                        scale: true,
                        gridIndex: 1,
                        splitNumber: 2,
                        axisLabel: { show: false },
                        axisLine: { show: false },
                        axisTick: { show: false },
                        splitLine: { show: false }
                    }
                ],
            series: [
                {
                    name: 'Price Candlestick',
                    type: 'candlestick',
                    data: [],
                    axisLabel: {
                        textStyle: {
                            fontSize: 12
                        },
                    },
                    itemStyle: {
                        color: 'rgb(0, 240, 0)',
                        color0: 'rgb(240, 0, 0)',
                        borderColor: 'rgb(0, 240, 0)',
                        borderColor0: 'rgb(240, 0, 0)',
                    },
                },
                {
                    name: 'Volume',
                    type: 'bar',
                    itemStyle: { color: 'rgb(0, 50, 128)', },
                    xAxisIndex: 1,
                    yAxisIndex: 1,
                    data: [],
                },
            ],
            backgroundColor: colorgraph,
        };

    }

}


