import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
//import { LoginService } from 'src/services/login/login.service';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { PortafolioService } from 'src/services/portafolio/portafolio.service';
import { ClientService } from 'src/services/Client/client.service';
import { SocketioService } from 'src/services/WebSocket/socketio.service';
import { PortafolioDefault } from 'src/models/Portafolio/portfolioDefault.model';
import { NgxSpinnerService } from 'ngx-spinner';
import { TestFoundsService } from 'src/services/TestFounds/test-founds.service';
import { BalanceModel, BalanceModelResponse } from 'src/models/Portafolio/portfolioBalance.model';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit, OnDestroy {

  title!:string;
  language: string;
  langs: string[] = [];
  portfolio_name: string = "";
  portfolioBalances:any;
  userName:string = "";
  totalCurrentBalance:number=0;
  usd_qty:any;
  show_data = [];
  page = 1;
  pageSize = 20;
  collectionSize:number;
  validating:any = false;
  subscription: Subscription = new Subscription();
  showdeleteaccount:boolean = false;

  constructor(private toastr:ToastrService,private assets:PortafolioService,
    private client:ClientService, private websocket:SocketioService,private testservice:TestFoundsService,
    private spinner:NgxSpinnerService) { 
  }

  ngOnInit(): void {
    this.InitHome();
    this.GetDataBloxOrder();
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }


  async deleteaccount(data:any){
    var del = {
      "code": data,
      "notes": "Yeah"
    } 
    var datareturn = await this.testservice.deleteaccount(del).toPromise();
    console.log("esto devuelve "+JSON.stringify(datareturn))

  }
  //===========================================================================
  //Socket Notificaciones
  //===========================================================================

  async GetDataBloxOrder() {
    this.subscription.add(this.websocket.GetDataBloxOrder().subscribe((data: any) => {
      if (typeof data != 'object' && data != "Hey Dude!") {
        if (JSON.parse(data).success == null || JSON.parse(data).success == undefined) {
          this.ResponseEvent(JSON.parse(data))
        }
      }
    }))
  }

  ResponseEvent(msg: any) {
    switch (msg.header.msg_type) {
      case 'EXECUTION':
      case 'TRACKING_EXECUTION':
      case 'DEPOSIT':
      case 'WITHDRAW':
      case 'ORDER_ERROR':
      case 'ORDER_CANCEL':
      case 'ORDER_REJECTED':
      case 'TRACKING_EXECUTION':
        console.log("Llego notificacion Socket");
        setTimeout(async () => {
          let portfolioBalancesResponse = await this.GetPortfolioBalances();
          if (!portfolioBalancesResponse) {
            this.ShowError("Error to get client portafolio balances");
            return;
          }
          this.usd_qty = portfolioBalancesResponse.find(element=> element.base_currency == 'USD').qty;
        
        }, 500);
        break;
      default:
        break;
    }
  }

  //===========================================================================
  //===========================================================================



  async InitHome(){
    try {
      this.spinner.show();
      //===========================================================================
      //Get client config
      //===========================================================================
      let getClientConfigResponse = await this.GetClientConfig();
      if (!getClientConfigResponse) {
        this.ShowError("Error to get client config");
        return;
      }

      // console.log("getClientConfigResponse:", getClientConfigResponse);
      if(getClientConfigResponse.email == "danielestebandh@gmail.com"){
        this.showdeleteaccount = true;
      }


      this.userName = this.CapitalizeName(getClientConfigResponse.fname);

      //===========================================================================
      //Get Default portafolio
      //===========================================================================
      let getDefaultPortafolioResponse = await this.GetDefaultPortafolio();
      if (!getDefaultPortafolioResponse) {
        this.ShowError("Error to get client portafolio");
        return;
      }

      // console.log("getDefaultPortafolioResponse:", getDefaultPortafolioResponse);
      this.portfolio_name = getDefaultPortafolioResponse.name;

      
      //===========================================================================
      //Get Portafolio Balance
      //===========================================================================
      let portfolioBalancesResponse = await this.GetPortfolioBalances();
      if (!portfolioBalancesResponse) {
        this.ShowError("Error to get client portafolio balances");
        return;
      }

      this.usd_qty = portfolioBalancesResponse.find(element=> element.base_currency == 'USD').qty;
      // console.log("USD ",this.usd_qty)

      // console.log("portfolioBalancesResponse:", portfolioBalancesResponse);

      portfolioBalancesResponse = portfolioBalancesResponse.filter(x=>x.qty > 0);
      // console.log("portfolioBalancesResponse after:", portfolioBalancesResponse);

      // console.log("portfolioBalancesResponse sort:", portfolioBalancesResponse);
      let products_id = portfolioBalancesResponse.filter(y=>y.fiat===false).map(x => `${x.base_currency}-USD`);
      // console.log("products_id:", products_id);

      portfolioBalancesResponse = portfolioBalancesResponse.map( x => {
        return{
          ...x,
          price: (x.base_currency === "USD") ? 1 : 0,
          total: (x.base_currency === "USD") ? x.qty : 0,
        };
      });

      this.totalCurrentBalance = portfolioBalancesResponse
      .map((x) => x.total)
      .reduce((a, b) => a + b, 0);

      // console.log("totalCurrentBalance:", this.totalCurrentBalance);


      this.portfolioBalances = portfolioBalancesResponse;

      if (products_id.length > 0) {
        //===========================================================================
        //Init Blox socket
        //===========================================================================
        this.websocket.InitBlox(products_id);

        this.subscription.add(this.websocket.GetDataBlox().subscribe(data =>{
          try {
            if (typeof data === 'string'){
              let coin = JSON.parse(data)
              //console.log("coin", coin);
      
              let product = this.portfolioBalances.find(x => `${x.base_currency}-USD` === coin.product_id);
              if (product) {
                //console.log("product", product);  
                product.price = coin.mid_price; 
                product.total = product.qty * (coin.mid_price ?? 1);
                //console.log("product", product);
      
                this.totalCurrentBalance = this.portfolioBalances
                .map((x) => x.total)
                .reduce((a, b) => a + b, 0);
              }
        
            }
            else{
              // console.warn("wrong data websocket", data);
            }
            
          } catch (error) {
            console.error("error websocket GetDataBlox", error);
          }      
      
        }));
      }
      else{
        console.log("portafolio without coins");
      }

      
    } catch (error) {
      console.error("Error on InitDeposit:", error);
    }
    this.spinner.hide();

  }
  
  GetDefaultPortafolio(): Promise<PortafolioDefault>{
    return new Promise<PortafolioDefault>((resolve, reject) => {
      this.subscription.add(this.assets.GetDefault().subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetDefault:", err);
        resolve(null);
      }));
    });
  }
  //***********************************************************************
  //Capitalize Name
  //***********************************************************************

  CapitalizeName(name: string) {
    if (!name){
      return name;
    }else{
      var word = name.split(' ');
      // console.log("Name",word)
      name = "";
      word.forEach(element => {
        name = name + element[0].toUpperCase() + element.substr(1).toLowerCase() + " ";
      });
      
      
      return name;

    }
  }

  //***********************************************************************
  // Websocket realtime value coins
  //***********************************************************************
  RealtimeCoinValue(data:any){
    try {
      if (typeof data === 'string'){
        let coin = JSON.parse(data)
        //console.log("coin", coin);

        let product = this.portfolioBalances.find(x => `${x.name}-USD` === coin.product_id);
        if (product) {
          //console.log("product", product);  
          product.price = coin.mid_price; 
          product.total = product.qty * (coin.mid_price ?? 1);
          //console.log("product", product);

          this.totalCurrentBalance = this.portfolioBalances
          .map((x) => x.total)
          .reduce((a, b) => a + b, 0);
        }
  
      }
      else{
        console.warn("wrong data websocket", data);
      }
      
    } catch (error) {
      console.error("error websocket GetDataBlox", error);
    }      
  }


  //***********************************************************************
  // Services Calls
  //***********************************************************************

  GetClientConfig(): Promise<any>{
    
    return new Promise((resolve, reject) => {
      this.subscription.add(this.client.GetClientConfig().subscribe(data=>{
        resolve(data);
      },err=>{
        console.log("Error GetClient:", err);
        resolve(null);
      }));
    });
  }


  GetPortfolioBalances(): Promise<BalanceModelResponse[]>{
    const portfolio:BalanceModel ={
      portfolio_name: this.portfolio_name
    }
    return new Promise<BalanceModelResponse[]>((resolve, reject) => {
      this.subscription.add(this.assets.GetPortafolioBalances(portfolio).subscribe(response=>{
        resolve(response);
      },err=>{
        console.log("Error GetAllPaymentMethods:", err);
        resolve(null);
      }));
    });
  }

  //***********************************************************************
  // Miscelaniuos
  //***********************************************************************


  ShowError(msg:string, title:string = "", disableTimeOut: boolean = true){
    console.error(msg);
    this.toastr.error(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }

  ShowWarning(msg:string, title:string = "", disableTimeOut: boolean = true){
    console.warn(msg);
    this.toastr.warning(msg, title, {
      closeButton: true,
      disableTimeOut: disableTimeOut,        
      positionClass: 'toast-center-center',        
    });
  }




}
